.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url(https://images.unsplash.com/photo-1501139083538-0139583c060f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80)
    no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  // position: absolute;
  z-index: -1;
}

.title {
  text-align: center;
  color: white;
  font-size: 5em;
  text-transform: uppercase;
}

.description-text {
  @extend .title;
  font-size: 3em;
  color: rgb(255, 0, 0);
}
